module.exports = {
	//appId
	getWxAppid() {
		return uni.getStorageSync("weixinAppid") ? uni.getStorageSync("weixinAppid") : ''
	},
	//appid
	getTaoBaoAppid() {
		return uni.getStorageSync("taobaoAppid") ? uni.getStorageSync("taobaoAppid") : ''
	},
	//Secret
	getTaoBaoSecret() {
		return uni.getStorageSync("taobaoSecret") ? uni.getStorageSync("taobaoSecret") : ''
	},
	//h5
	getTaoBaoRedirect() {
		return uni.getStorageSync("taobaoUrl") ? uni.getStorageSync("taobaoUrl") :
			''
	},
	//  tb_name
	getTaoBaoName() {
		return uni.getStorageSync("taobaoName") ? uni.getStorageSync("taobaoName") : ''
	},
	//PID
	getTaoBaoPid() {
		return uni.getStorageSync("taobaoPid") ? uni.getStorageSync("taobaoPid") : ''
	},
	//Key
	getTaoBaoKey() {
		return uni.getStorageSync("haodankuKey") ? uni.getStorageSync("haodankuKey") : ''
	},
	//
	getInvitation() {
		return uni.getStorageSync("publicRelation") ? uni.getStorageSync("publicRelation") : ''
	},
	// html config
	publicYuMing() {
		return 'https://www.gomyorder.cn'
	},

	logout() {
		this.remove("token");
		this.remove("userId");
		this.remove("mobile");
		this.remove("openid");
		this.remove("nickName");
		this.remove("relation");
		this.remove("image_url");
		this.remove("relation_id");
	},
	loginClear() {
		this.remove("token");
		this.remove("userId");
		this.remove("mobile");
		this.remove("nickName");
		this.remove("image_url");
		this.remove("relation_id");
	},
	showLoading(title) {
		uni.showLoading({
			title: title
		});
	},
	showToast(title) {
		uni.showToast({
			title: title,
			mask: false,
			duration: 2000,
			icon: "none"
		});
	},
	getSearchKeys: function(key) {
		let list = "                   ";
		return list.includes(key);
	},
	setJson: function(key, value) {
		let jsonString = JSON.stringify(value);
		try {
			uni.setStorageSync(key, jsonString);
		} catch (e) {
			// error
		}
	},
	setData: function(key, value) {
		try {
			uni.setStorageSync(key, value);
		} catch (e) {
			// error
		}
	},
	getData: function(key) {
		try {
			const value = uni.getStorageSync(key);
			if (value) {
				return value;
			}
		} catch (e) {
			// error
		}

	},
	getJson: function(key) {
		try {
			const value = uni.getStorageSync(key);
			if (value) {
				return JSON.parse(value);
			}
		} catch (e) {
			// error
		}

	},
	clear: function() {
		uni.clearStorage();
	},
	get: function(key) { //
		let data = this.getJson(key);
		if (data instanceof Array) {
			return data;
		}
		return [];
	},
	insert: function(param) { //
		param.capacityNum = param.capacityNum || 100; // 100
		let data = this.getJson(param.key);
		if (data instanceof Array) {
			if (data.length > param.capacityNum) {
				let total = data.length - param.capacityNum;
				for (let i = 0; i < total; i++) {
					data.pop();
				}
			}
			data.unshift(param.value);
		} else {
			data = [];
			data.push(param.value);
		}
		this.setJson(param.key, data);
	},
	removeItem: function(key, itemIds) { //itemIds 
		let data = this.getJson(key);
		if (data instanceof Array) {
			for (let i = 0; i < itemIds.length; i++) {
				for (let p = 0; p < data.length; p++) {
					if (itemIds[i] === data[p].itemid) {
						data.splice(p, 1);
						break;
					}
				}
			}
			this.setJson(key, data);
		}
	},
	isExist: function(key, itemId) { //
		let data = this.getJson(key);
		if (data instanceof Array) {
			for (let p = 0; p < data.length; p++) {
				if (itemId === data[p].itemid) {
					return true;
				}
			}
		}
		return false;
	},
	remove: function(key) { //
		try {
			uni.removeStorageSync(key);
			//localStorage.removeItem(key)
		} catch (e) {
			// error
		}
	},
	getCount: function(key) { //

		let data = this.getJson(key);
		if (data instanceof Array) {
			return data.length;
		}
		return 0;
	},
};
